import React from "react";
import "./App.css";
import Button from "@material-ui/core/Button";
import Checkbox from "@material-ui/core/Checkbox";
import FormControlLabel from "@material-ui/core/FormControlLabel";
import blue from "@material-ui/core/colors/blue";
import purple from "@material-ui/core/colors/purple";
import { MuiThemeProvider, createMuiTheme } from "@material-ui/core";
import AppHeader from "./AppHeader";
import Box from "@material-ui/core/Box";
import Typography from "@material-ui/core/Typography";
import Container from "@material-ui/core/Container";
import GridComponent from "./components/grid-comp";
import logo from "./logo.jpg";
import logo1 from "./flash.png";

// const primary = blue[500];

function App() {
  const [state, setState] = React.useState({
    isDarkMode: false,
    drawer: false,
    display: "",
  });
  const toggleDrawer = (open) => (event) => {
    if (
      event.type === "keydown" &&
      (event.key === "Tab" || event.key === "Shift")
    ) {
      return;
    }
    setState({ ...state, drawer: open });
  };
  const toggleMode = () => {
    setState({ ...state, isDarkMode: !state.isDarkMode });
  };
  const toggleDisplay = (d) => (event) => {
    setState({ ...state, display: d, drawer: false });
  };
  const theme1 = createMuiTheme({
    palette: {
      primary: blue,
      secondary: {
        main: blue[500],
      },
      type: "light",
    },
  });
  const theme2 = createMuiTheme({
    palette: {
      primary: purple,
      secondary: {
        main: purple[500],
      },
      type: "dark",
    },
  });

  function renderSwitch(param) {
    switch (param) {
      case "box":
        return (
          <Box bgcolor="primary.main" color="primary.contrastText" p={2}>
            primary.main
          </Box>
        );
      case "container":
        return (
          <Container fixed>
            <Typography
              component="div"
              style={{ backgroundColor: "#cfe8fc", height: "90vh" }}
            />
          </Container>
        );
      case "grid":
        return <GridComponent></GridComponent>;
      case "button":
        return (
          <Button variant="contained" color="secondary">
            Hello World
          </Button>
        );
      case "checkbox":
        return (
          <FormControlLabel
            control={<Checkbox value="secondary" color="primary" />}
            label="Primary"
          />
        );
      default:
        return <div></div>;
    }
  }

  return (
    <MuiThemeProvider theme={state.isDarkMode ? theme2 : theme1}>
      <div>
        <div style={{display:"flex"}}>
          <img
            src={logo}
            alt="Logo"
            style={{ height: "85px", width: "85px" }}
          />
          <img src={logo1} alt="Logo" className="logo2" />
          <div style={{marginTop:"25px",display:"flex",flexDirection:"column"}}>
            <span
              style={{
                fontStyle: "italic",
                fontSize: "25px",
                color: "#26ad0c",
                fontWeight:"500"
              }}
            >
              3 A Compliance
            </span>
            <span
            style={{
              fontSize: "12px",
              color: "rgb(113 9 24)",
            }}
            >Certification Made Easy</span>
          </div>
        </div>

        <AppHeader
          mode={state.isDarkMode}
          drawer={state.drawer}
          toggleM={toggleMode}
          toggleD={toggleDrawer}
          display={toggleDisplay}
        />
        <div style={{ height: "calc(100vh - 54px)", marginTop: "60px" }}>
          {renderSwitch(state.display)}
        </div>
      </div>
    </MuiThemeProvider>
  );
}

export default App;
